export default {
	data() {
		return {
			PageCur: '发现',
			modalNameS1: null,
			modalNameS2: null,
			gridCol: 3,
			gridBorder: false,
			menuBorder: false,
			menuArrow: false,
			menuCard: false,
			skin: false,
			listTouchStart: 0,
			listTouchDirection: null
		}
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		showModalS1(e) {
			this.modalNameS1 = e.currentTarget.dataset.target
		},
		hideModalS1(e) {
			this.modalNameS1 = null
		},
		showModalS2(e) {
			this.modalNameS2 = e.currentTarget.dataset.target
		},
		hideModalS2(e) {
			this.modalNameS2 = null
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		
	}
}
